import React from "react";
import "./index2.css";
const TodoList=(probs)=>
{
   
    
    return(
        <>

        <div className="todo_style">
        <i className="fa fa-times" aria-hidden="true" onClick={() =>
        {
                probs.onSelect(probs.id);
        }}
        />
   <li>{probs.text} </li>
   </div>
    
    </>
      );
};
export default TodoList;